import { Core, Fetch } from "@true/core";
import { batch } from "react-redux";
import { IRequestPolicyDoctypeParams } from "@policy/store/policies/functions";
import { setMasterVersionDoctypesQuery, syncMasterVersionDoctypes } from "@policy/store/masterVersionDoctypes/index";
import { IMasterVersionDoctypes } from "@policy/store/masterVersionDoctypes/types";

export interface IRequestMasterVersionDoctypeParams {
  id: string | number;
  page?: number;
}

export const requestMasterVersionDoctype = async (
  params: IRequestPolicyDoctypeParams,
) => {
  const {data} = await Fetch.postWithToken<{
    mvs: IMasterVersionDoctypes[];
  }>(`@${Core.systemPath}/policy/ajax/api/mobile/doc/doctype.mvs`, params);

  batch(() => {
    if (data.mvs) {
      syncMasterVersionDoctypes(data.mvs);

      const newDocs = data.mvs.map(item => item.id.toString())

      setMasterVersionDoctypesQuery(prev => ({
        ...prev,
        all: params.page && params.page > 1
          ? [ ...prev.all, ...newDocs]
        : newDocs,
      }));
    }
  });

  return data.mvs;
};

interface IRequestEditMasterVersion {
  name: string;
  collection_id: number | string;
  doctype_id: number | string;
  id: number | string;
}

export const requestEditMasterVersion = async (
  params: IRequestEditMasterVersion,
) => {
  const {data} = await Fetch.postWithToken<{
    message: string;
  }>(`@${Core.systemPath}/policy/ajax/api/doc/mv/edit`, params);


  batch(() => {
    if (data.message == "ok") {
      syncMasterVersionDoctypes(prev => [{
        ...prev?.[params.id],
        doctype_id: params.doctype_id as number,
        collection_id: params.collection_id as number,
        name: params.name
      }]);
    }
  });

  return data.message;
};

interface IRequestRemoveMasterVersion {
  id: number | string;
}

export const requestRemoveMasterVersion = async (
  params: IRequestRemoveMasterVersion,
) => {
  const {data} = await Fetch.postWithToken<{
    message: string;
  }>(`@${Core.systemPath}/policy/ajax/api/doc/mv/remove`, params);


  batch(() => {
    if (data.message == "ok") {
      setMasterVersionDoctypesQuery(prev => ({
        ...prev,
        all: prev.all.filter(id => id != params.id)
      }))
    }
  });

  return data.message;
};
