import React, {memo, useCallback} from 'react';
import {
  BottomButton,
  DynamicHeader,
  FormScroll,
  MyKeyboardAvoidingView,
  ScreenWrapper,
  UISelectorInput,
  UITextInput,
} from '@true/ui';
import {
  goBack,
  styled,
  translate,
  useAsyncFn,
  useAutoToastError,
  useNavigationParams,
  useTheme,
} from '@true/core';
import {useMasterVersionDoctypes} from '@policy/store/masterVersionDoctypes';
import {getCollections, useCollectionsByQuery} from '@policy/store/collections';
import {getDoctypes, useDoctypesByQuery} from '@policy/store/doctypes';
import {AnimatedBottomSpace} from '@policy/components/AnimatedBottomSpace';
import {runOnUI, useSharedValue} from 'react-native-reanimated';
import {requestEditMasterVersion} from '@policy/store/masterVersionDoctypes/functions';

export interface EditMasterVersionProps {
  id: number;
}

export const EditMasterVersion = memo(() => {
  const {id} = useNavigationParams<EditMasterVersionProps>();

  const masterVersion = useMasterVersionDoctypes(id);

  const theme = useTheme();

  const params = useSharedValue({
    name: masterVersion?.name || '',
    collection_id: masterVersion?.collection_id || '',
    doctype_id: masterVersion?.doctype_id || '',
  });

  const collections = useCollectionsByQuery('all').map(_id => {
    const _item = getCollections(_id);
    return {
      label: _item?.name,
      value: _id,
    };
  }) as any;

  const doctypes = useDoctypesByQuery('all').map(_id => {
    const _item = getDoctypes(_id);
    return {
      label: _item?.name,
      value: _id,
    };
  }) as any;

  // const onChangeParam = useCallback((keyName: string, value: any) => {
  //   setParams(prev => ({
  //     ...prev,
  //     [keyName]: value,
  //   }));
  // }, []);

  const [{loading, error}, onSubmit] = useAsyncFn(async () => {
    return await requestEditMasterVersion({id, ...params.value}).then(() =>
      goBack(),
    );
  }, [params, id]);

  console.log('params', params);

  useAutoToastError(error);

  return (
    <ScreenWrapper>
      <DynamicHeader title={translate('policy.edit_master_version_info')} />
      <MyKeyboardAvoidingView>
        <FormScroll>
          <SWrapper>
            <UITextInput
              params={params}
              title={translate('policy.master_version_name')}
              required={true}
              keyName={'name'}
            />
          </SWrapper>
          <SWrapper>
            <UISelectorInput
              title={'Collection'}
              required={true}
              params={params}
              keyName={'collection_id'}
              options={collections}
            />
          </SWrapper>
          <SWrapper>
            <UISelectorInput
              title={'Document type'}
              required={true}
              params={params}
              keyName={'doctype_id'}
              options={doctypes}
            />
          </SWrapper>
        </FormScroll>
        <SButtonWrapper>
          <BottomButton
            loading={loading}
            onRightPress={onSubmit}
            leftContainerStyle={{
              borderRadius: 8,
              borderColor: theme.primaryColor,
              borderWidth: 1,
            }}
            rightContainerStyle={{borderRadius: 8}}
            leftBgColor={theme.white}
            textColor={theme.primaryColor}
            leftText={translate('policy.reset')}
          />
          <AnimatedBottomSpace />
        </SButtonWrapper>
      </MyKeyboardAvoidingView>
    </ScreenWrapper>
  );
});

const SWrapper = styled.View`
  margin: 16px 16px 0;
`;

const SButtonWrapper = styled.View`
  margin: 16px 16px 0;
`;
