import React, {memo} from 'react';
import {useAnimatedSafeAreaInsets} from 'react-native-safe-area-context';
import Animated, {useAnimatedStyle} from 'react-native-reanimated';
import {ViewProps} from 'react-native';
import {flatStyles} from '@true/core';

export const AnimatedBottomSpace = memo((props: ViewProps) => {
  const {aBottom} = useAnimatedSafeAreaInsets();
  const animatedStyle = useAnimatedStyle(() => ({
    height: aBottom.value,
  }));

  return <Animated.View {...props} style={[props.style, animatedStyle]} />;
});
